'use client'
import { useState, useEffect } from 'react';
import { listenPedidos } from '../../serverless/db/listenPedidos';
import SacoUi from './../ui/SacoUi';
import Navbar from './../components/Navbar/';

const ListDocuments = () => {
  const [pedidos, setPedidos] = useState([]);
  // State to hold the selected date, initialized to today
  const [selectedDate, setSelectedDate] = useState(new Date().toISOString().split('T')[0]);

  useEffect(() => {
    // The listener now depends on selectedDate
    const unsubscribe = listenPedidos((newPedidos) => {
      setPedidos(newPedidos);
    }, selectedDate);

    // Cleanup the listener when the component unmounts or the date changes
    return () => unsubscribe();
  }, [selectedDate]); // Re-run the effect when selectedDate changes

  const handleDateChange = (event) => {
    setSelectedDate(event.target.value);
  };

  const handleDownload = (pedido) => {
    const { id, cliente, fecha, items, total, pedidoInfo } = pedido;
    const message = `*Pedido #${id}*
*Cliente:* ${cliente}
*Fecha:* ${fecha}

*Detalles del Pedido:*
${items.map(item => `- ${item.nombre} (x${item.cantidad}) - $${(item.precio * item.cantidad).toFixed(2)}`).join('\n')}

*Total:* $${total.toFixed(2)}`;

    const blob = new Blob([message], { type: 'text/plain' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `pedido-${id}.txt`;
    a.click();
    URL.revokeObjectURL(url);
  };

  const handleShare = (pedido) => {
    const { id, cliente, fecha, items, total, pedidoInfo } = pedido;
    const message = `*Pedido #${id}*
*Cliente:* ${cliente}
*Fecha:* ${fecha}

*Detalles del Pedido:*
${items.map(item => `- ${item.nombre} (x${item.cantidad}) - $${(item.precio * item.cantidad).toFixed(2)}`).join('\n')}

*Total:* $${total.toFixed(2)}`;

    const phone = pedidoInfo.phone; 
    const whatsappUrl = `https://wa.me/${phone}?text=${encodeURIComponent(message)}`;
    window.open(whatsappUrl, '_blank');
  };

  return (
    <>
      <Navbar />
      <main className="container mx-auto p-4">
        <div className="flex justify-between items-center mb-6">
          <h1 className="text-4xl font-bold text-gray-800">Pedidos</h1>
          <div className="flex items-center space-x-4">
             <label htmlFor="date-picker" className="text-lg font-medium text-gray-600">Seleccionar Fecha:</label>
             <input 
                type="date" 
                id="date-picker"
                value={selectedDate}
                onChange={handleDateChange}
                className="px-4 py-2 border border-gray-300 rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
             />
          </div>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {pedidos.length > 0 ? (
            pedidos.map((pedido) => (
              <div key={pedido.id} className="bg-white rounded-xl shadow-lg overflow-hidden transform hover:scale-105 transition-transform duration-300 ease-in-out">
                <div className="p-6">
                  <div className="flex justify-between items-start">
                    <div>
                      <h2 className="text-2xl font-bold text-gray-900 mb-2">{pedido.cliente}</h2>
                      <p className="text-sm text-gray-500 mb-1">#{pedido.id}</p>
                      <p className="text-sm text-gray-500 mb-4">{pedido.fecha}</p>
                    </div>
                    <div className="text-3xl font-extrabold text-blue-600">${pedido.total.toFixed(2)}</div>
                  </div>

                  <ul className="space-y-3 mb-6">
                    {pedido.items.map((item, index) => (
                      <li key={index} className="flex justify-between items-center text-gray-700">
                        <span>{item.nombre} <span className="text-gray-500">x{item.cantidad}</span></span>
                        <span className="font-medium">${(item.precio * item.cantidad).toFixed(2)}</span>
                      </li>
                    ))}
                  </ul>

                  <div className="flex justify-end space-x-3">
                    <button 
                      onClick={() => handleDownload(pedido)} 
                      className="px-4 py-2 bg-gray-200 text-gray-800 rounded-lg hover:bg-gray-300 transition-colors focus:outline-none focus:ring-2 focus:ring-gray-400"
                    >
                      Descargar
                    </button>
                    <button 
                      onClick={() => handleShare(pedido)} 
                      className="px-4 py-2 bg-green-500 text-white rounded-lg hover:bg-green-600 transition-colors focus:outline-none focus:ring-2 focus:ring-green-400"
                    >
                      Compartir
                    </button>
                  </div>
                </div>
              </div>
            ))
          ) : (
            <div className="col-span-full text-center py-12">
              <SacoUi classname="w-40 h-40 mx-auto text-gray-300"/>
              <p className="text-xl text-gray-500 mt-4">No se encontraron pedidos para la fecha seleccionada.</p>
            </div>
          )}
        </div>
      </main>
    </>
  );
};

export default ListDocuments;
